import React, { Component } from 'react';
import { Input, DatePicker, Button,Form } from 'antd';

const { RangePicker } = DatePicker;

class SearchBox extends Component {
    handleSearch = (e,name) => {
        console.log(e,name);
        e && e.preventDefault();
        if(name&&name==="reset"){
            console.log("");
            let params = {
                templetName:"",
                createByOrDeptName:"",
                updateTimeStart:"",
                updateTimeEnd:"",
              }
            this.props.search(params,"reset");
        }else{
            this.props.form.validateFields((err, values) => {
                if (!err) {
                //   console.log('Received values of form: ', values);
                  let params = {
                    templetName:values.templetName || "",
                    createByOrDeptName:values.createByOrDeptName || "",
                    updateTimeStart:values.time?values.time[0].format("YYYY-MM-DD HH:mm:ss"):"",
                    updateTimeEnd:values.time?values.time[1].format("YYYY-MM-DD HH:mm:ss"):"",
                  }
                //   console.log(params);
                this.props.search(params);
                }
            });
        }
    }
    _reset = () => {
        this.props.form.resetFields();
        this.handleSearch("","reset");
    }
    render() {
        const { getFieldDecorator } = this.props.form;
        return (
            <Form layout="inline" onSubmit={(e)=>{
                this.handleSearch(e,"")}
                }>
                <Form.Item label="">
                    {
                        getFieldDecorator("templetName",{})(<Input placeholder="" />)
                    }
                </Form.Item>
                <Form.Item label="/">
                    {
                        getFieldDecorator("createByOrDeptName",{})(<Input placeholder="/" />)
                    }
                </Form.Item>
                <Form.Item label="">
                    {
                        getFieldDecorator("time",{})(<RangePicker style={{width:330}} format="YYYY-MM-DD HH:mm:ss" showTime={{ format: 'HH:mm:ss' }}/>)
                    }
                </Form.Item>
                <Form.Item>
                    <Button type="primary" icon="search"  htmlType="submit" ghost></Button>
                </Form.Item>
                <Form.Item>
                    <Button icon="sync" onClick={this._reset} ></Button>
                </Form.Item>
            </Form>
        )
    }
}

export default Form.create({})(SearchBox)
