import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Button } from 'antd';
import FilterBox from './filterbox';
import IndexTable from './indexTable';
import styles from './index.module.less';
import SystemTabs from '../common/systemTabs';
import {hasButtonPermission} from '@/util'

class SummaryTemplateManagement extends Component {
    state = {
        filterValue:{
            templetName:"",
            createByOrDeptName:"",
            updateTimeStart:"",
            updateTimeEnd:"",
        }
    }
    //
    _updateFilterValue = (filterValue,name) => {
        if(name&&name==="reset"){
            filterValue.name= "reset";
            this.setState({
                filterValue:{...filterValue}
            })
        }else{
            this.setState({
                filterValue:{...filterValue}
            }) 
        }
    }
    //
    _add = () => {
        this.props.history.push("/systemSettings/summaryTemplateManagement/add")
    }
    render() {
        return (
            <React.Fragment>
                <SystemTabs activeTab="summaryTemplateManagement"/>
                <div className={styles.container}>
                    <div className={styles.card}>
                        <FilterBox search={(filterValue,name)=>{this._updateFilterValue(filterValue,name)}}/>
                        {
                            hasButtonPermission("formTemplate001")?<div style={{margin:"10px 0"}}>
                                <Button type="primary" onClick={this._add}></Button>
                            </div>:null
                        }
                        <IndexTable filterValue={this.state.filterValue}/>
                    </div>
                </div>
            </React.Fragment>
           
        )
    }
}

export default withRouter(SummaryTemplateManagement);
