import React, { Component } from 'react';
import { Table, Switch, Divider, Popconfirm, message,Modal } from 'antd';
import { postAction, getAction } from '@/axios';
import { withRouter } from 'react-router-dom';
import PrivilegeModal from './privilegeModal';
import {hasButtonPermission} from '@/util'

class IndexTable extends Component {
    constructor(props){
        super(props);
        this.state = {
            columns:[
                {
                    title:"",
                    dataIndex:"sortIndex",
                    width:'60px'
                },{
                    title:"",
                    dataIndex:"templetName",
                    // width:'360px'
                },{
                    title:"",
                    dataIndex:"updateTime",
                    width:'180px'
                },{
                    title:"",
                    dataIndex:"createBy",
                    width:'120px'
                },{
                    title:"",
                    dataIndex:"createDeptName",
                    width:'120px'
                },{
                    title:"",
                    dataIndex:"status",
                    width:'120px',
                    render:(text,record)=>{
                        return <Switch checkedChildren="" unCheckedChildren="" checked={text===1?true:false} onChange={(e)=>{this._changeStatus(record,e)}}/>
                    }
                },{
                    title:"",
                    width:'180px',
                    // fixed:"right",
                    render:(text,record)=>{
                        return (
                            <React.Fragment>
                                {
                                   hasButtonPermission("formTemplate002")?<span onClick={()=>{this._edit(record)}} className="operation-link"></span>:null
                                }
                                <Divider type="vertical"/>
                                {
                                    hasButtonPermission("formTemplate003")?<Popconfirm
                                        title=""
                                        onConfirm={()=>{this._delete(record)}}
                                    >
                                        <span className="operation-link"></span>
                                    </Popconfirm>:null
                                }
                                <Divider type="vertical"/>
                                {
                                    hasButtonPermission("formTemplate004")?<span className="operation-link" onClick={()=>{this._changePrivilege(record)}}></span>:null
                                }
                            </React.Fragment>
                        )
                    }
                }
            ],
            dataSource:[
                // {createBy: "",createDeptName: "",status: 1,templetId: "16f55a5d93224235a484790465c08206",templetName: "",updateTime: "2019-09-27 00:35:20"}
            ],
            pagination:{
                current:1,
                pageSize:10,
                total:0,
                showTotal:(total)=>`${total}`,
                showQuickJumper:true,
            },
            filterValue:{
                templetName:"",
                createByOrDeptName:"",
                updateTimeStart:"",
                updateTimeEnd:"",
            },
            showprivilegeModal:false,//
            currentTemplateInfo:{},//
            privilegeTreeData:[],//
        }
    }
    componentDidMount(){
        this._getData();
        this._getPrivilegeTreeRequest();
    };
    componentDidUpdate(preProps,preState){
        let filterValue = this.props.filterValue;
        if(JSON.stringify(filterValue)!==JSON.stringify(preProps.filterValue)){
            if(filterValue.name&&filterValue.name==="reset"){
                let pagination = this.state.pagination;
                pagination.current=1;
                delete filterValue.name;
                this.setState({
                    filterValue:{...filterValue},
                    pagination
                },this._getData)
            }else{
                delete filterValue.name;
                this.setState({
                    filterValue:{...filterValue}
                },this._getData)
            }
            
        }
    }
    //
    _getPrivilegeTreeRequest = () => {
        postAction('/ybt/system/setting/depart/getDepartList').then(res=>{
            if(res.success){
                this.setState(({privilegeTreeData:res.obj || []}))
            }
        })
    }
    //
    _changeStatus = (record,e) => {
        getAction(`ybt/summary/effective?templetId=${record.templetId}&status=${e?1:2}`).then(res=>{
            if(res.success){
                message.success("");
                this._getData();
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _edit = (record) => {
        // console.log(record);
        this.props.history.push(`/systemSettings/summaryTemplateManagement/edit?templetId=${record.templetId}`);
    }
    //
    _delete = (record) => {
        // console.log(record);
        getAction(`/ybt/summary/delete?templetId=${record.templetId}`).then(res=>{
            if(res.success){
                message.success("");
                this._getData();
            }else{
                message.error(res.obj);
            }
        })
    }
    //
    _changePrivilege = (record) => {
        // console.log(record);
        this.setState({currentTemplateInfo:record,showprivilegeModal:true})
    }
    //
    _changeTable = (pagination) => {
        this.setState((state)=>(
            {pagination:{...state.pagination,current:pagination.current}}
        ),()=>{
            this._getData("byPage")
        })
    }
    //
    _getData = (byPage) => {
        let params = {
            ...this.state.filterValue
        };
        let pageNum = 1;
        let pageSize = this.state.pagination.pageSize;
        if(byPage==="byPage"){
            pageNum = this.state.pagination.current
        }
        postAction(`/ybt/summary/list?pageNum=${pageNum}&pageSize=${pageSize}`,params).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState((state)=>{
                    return {
                        dataSource:res.obj.rows.map((item,index)=>({...item,sortIndex:index+1})),
                        pagination:{...state.pagination,current:res.obj.page,total:res.obj.total}
                    }
                })
            }else{
                message.error(res.obj);
            }
        })
    }
    //
    _cancle = () => {
        this.setState({showprivilegeModal:false,currentTemplateInfo:{}})
    }
    //
    _ok = (data) => {
        // console.log(data);
        postAction('/ybt/summary/departPerm',data).then(res=>{
            if(res.success){
                this.setState({
                    currentTemplateInfo:{},
                    showprivilegeModal:false
                });
                this._getData();
            }else{
                message.error(res.obj);
            }
        })
    }
    render() {
        return (
            <React.Fragment>
                <Table
                    rowKey="templetId"
                    columns={this.state.columns}
                    dataSource={this.state.dataSource}
                    pagination={this.state.pagination}
                    onChange={this._changeTable}
                />
                <Modal
                    visible={this.state.showprivilegeModal}
                    title=""
                    footer={false}
                    onCancel={this._cancle}
                    bodyStyle={{paddingTop:"5px"}}
                    width={800}
                    destroyOnClose={true}
                >
                    <PrivilegeModal 
                        privilegeTreeData={this.state.privilegeTreeData} 
                        onOk={(data)=>{this._ok(data)}} 
                        onCancel={this._cancle}
                        templateInfo={this.state.currentTemplateInfo}
                    />
                </Modal>
            </React.Fragment>
            
        )
    }
}


export default withRouter(IndexTable);