import React, { Component } from 'react';
import { Button, Tree, Checkbox, message } from 'antd';
import { postAction } from '@/axios';

let expendTree = (orginData,data) => {
    orginData.forEach(item=>{
        data.push({departId:item.departId,departName:item.departName});
        if(item.children){
            expendTree(item.children,data)
        }
    });
    return data;
}

export default class PrivilegeModal extends Component {
    constructor(props){
        super(props);
        this.state = {
            templateInfo:{},
            checkedKeys:[],
            checkedNodes:[],
            departList:[],//
        }
    }
    componentDidMount(){
        // console.log(this.props.templateInfo);
        this.setState({
            departList:expendTree(this.props.privilegeTreeData,[]),
            templateInfo:this.props.templateInfo || {}
        });
        this._getPrivilegeRequest(this.props.templateInfo.templetId)
    }
    //
    _getPrivilegeRequest = (templateId) => {
        postAction(`/ybt/summary/getTemplatePermList?templetId=${templateId}`).then(res=>{
            // console.log(res);
            if(res.success){
                this.setState({
                    checkedKeys:res.obj,
                    checkedNodes:this.state.departList.filter(item=>res.obj.indexOf(item.departId)!==-1)
                })
            }else{
                message.error(res.obj)
            }
        })
    }
    //
    _renderTree = (data=[]) =>{
        return data.map(item => <Tree.TreeNode key={item.departId} value={item.departId} title={item.departName}>{this._renderTree(item.children)}</Tree.TreeNode>);
    } 
    //
    _onCheck = (checkedKeys,e ) => {
        this.setState({checkedKeys,checkedNodes:e.checkedNodes.map(item=>({departId:item.props.value,departName:item.props.title}))})
    }
    //
    _changeRightCheck = (checkedKeys) => {
        this.setState({
            checkedKeys,
            checkedNodes:this.state.departList.filter(item=>checkedKeys.indexOf(item.departId)!==-1)
        })
    }
    //
    _save = () => {
        let templetId = this.state.templateInfo.templetId;
        let templetName = this.state.templateInfo.templetName;
        let data = this.state.checkedNodes.map(item=>({...item,templetName,templetId}));
        this.props.onOk(data);
    }
    render() {
        const containerStyle = {border:"1px solid #D9D9D9",borderRadius:"16px",height:"440px",overflow:"auto",marginTop:"5px",padding:"5px 10px"};
        return (
            <React.Fragment>
                <div style={{display:"flex"}}>
                    <div style={{flex:"1",paddingRight:40}}>
                        
                        <div style={containerStyle}>
                        <Tree
                            checkable
                            onCheck={this._onCheck}
                            checkedKeys={this.state.checkedKeys}
                        >
                            {this._renderTree(this.props.privilegeTreeData)}
                        </Tree>
                        </div>
                    </div>
                    <div style={{flex:"1",paddingLeft:40}}>
                        
                        <div style={containerStyle}>
                            <Checkbox.Group value={this.state.checkedKeys} onChange={this._changeRightCheck}>
                                {
                                    this.state.checkedNodes.map(item=>{
                                        return <div key={item.departId}><Checkbox value={item.departId}>{item.departName}</Checkbox></div>
                                    })
                                }
                            </Checkbox.Group>
                        </div>
                    </div>
                </div>
                <div style={{textAlign:"right",marginTop:"20px"}}>
                    <Button type="primary" style={{marginRight:"20px"}} onClick={this._save}> </Button>
                    <Button onClick={this.props.onCancel}> </Button>
                </div>
            </React.Fragment>
            
        )
    }
}
